




























import {Vue, Component, Provide} from 'vue-property-decorator'

@Component
export default class ControllingUpdates extends Vue {
    arr = [1, 2, 3, 4]
    msg = 'hi'

    toChange() {
        const index = this.$refs.arrIndex.value
        const value = this.$refs.arrValue.value

        this.arr[index] = value
    }

    toRefresh() {
        this.$forceUpdate()
    }
}
