module.exports = "<!--\r\n1. 依赖注入\r\n数据是非响应式的\r\n-->\r\n\r\n<template>\r\n    <div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>1. 依赖注入</h4>\r\n            <el-button @click=\"update\">update</el-button>\r\n            <el-button @click=\"cancelWatch\">取消监听</el-button>\r\n        </div>\r\n\r\n\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component, Provide} from 'vue-property-decorator'\r\n    import $Vue from 'vue/dist/vue.js'\r\n\r\n    $Vue.component('child-component', {\r\n        methods: {\r\n            update () {\r\n                this.$emit('update')\r\n            }\r\n        },\r\n        template: '<button @click=\"update\">update</button>'\r\n    })\r\n\r\n\r\n    @Component({\r\n        components: {\r\n            'child-component': $Vue.component('child-component')\r\n        }\r\n    })\r\n\r\n    export default class ProgrammaticEventListeners extends Vue {\r\n        @Provide() provideData = 'from ElementComponentAccess'\r\n\r\n        mounted() {\r\n            this.$on('update', function () {\r\n                this.$notify({\r\n                    title: 'child update',\r\n                    message: '无限次监听'\r\n                });\r\n            })\r\n            this.$once('update', ()=> {\r\n                setTimeout(() => {\r\n                    this.$notify({\r\n                        title: 'child update',\r\n                        message: '只监听一次'\r\n                    });\r\n                })\r\n            })\r\n        }\r\n\r\n        cancelWatch() {\r\n            this.$off('update')\r\n            this.$notify({\r\n                title: 'child update',\r\n                message: '取消监听'\r\n            });\r\n        }\r\n\r\n        update() {\r\n            this.$emit('update')\r\n        }\r\n\r\n    }\r\n</script>\r\n";