module.exports = "<!--\n1. 访问根实例\n状态较多的时候通常不建议这么做\n\n2. 访问父级组件实例\n通常不建议这么做，会增加状态追踪复杂度。\n-->\n\n<template>\n    <div>\n\n        <div class=\"case-item\">\n            <h4>1. 访问根实例</h4>\n            <div>from: {{ this.$root.from}}</div>\n        </div>\n\n        <div class=\"case-item\">\n            <h4>2. 访问父级组件实例</h4>\n            <child-component></child-component>\n        </div>\n\n        <div class=\"case-item\">\n            <h4>3. 访问子组件实例或子元素</h4>\n            <base-input ref=\"usernameInput\"></base-input>\n            <el-button @click=\"inputFocus\">focus</el-button>\n        </div>\n\n    </div>\n</template>\n\n<script lang=\"ts\">\n    import {Vue, Component, Watch} from 'vue-property-decorator'\n    import $Vue from 'vue/dist/vue.js'\n\n    $Vue.component('child-component', {\n        template: '<div>From: {{ this.$parent.from }}</div>'\n    })\n\n    $Vue.component('base-input', {\n        methods: {\n            // 用来从父级组件聚焦输入框\n            focus() {\n                this.$refs.input.focus()\n            }\n        },\n        template: '<input ref=\"input\">'\n    })\n    @Component({\n        components: {\n            'child-component': $Vue.component('child-component'),\n            'base-input': $Vue.component('base-input')\n        }\n    })\n\n    export default class ElementComponentAccess extends Vue {\n        public from = 'parent'\n\n        public inputFocus() {\n            this.$refs.usernameInput.focus()\n        }\n    }\n</script>\n";